*{
    margin: 0;
}

li{
    list-style: none;
}

.container{
    text-align: center;
    z-index: 1;
}

.content{
    color: rgba(219, 219, 220, 0.943);
    position:relative;
    z-index: 1;
}

.content article img{
    width: 400px;
}

.bgs img, .bgg{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 0;
}

.bgs img{
    width: 100%;
    background:linear-gradient(rgba(0, 0, 0, 0), black);
    height: 100%;
}

.bgg{
    background:linear-gradient(rgba(0, 0, 0, 0), black);
    height: 100%;
}

@media (width>790px) {
 
    .content{  
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 2fr 2fr;
    margin: 0px 71px;
    top: 134px;
    grid-row-start: -5;
    }

    .content img{
        grid-row: 1 / span 6;
        width: 500px;
    }

    .content span{
        width: 800px;
        grid-row-start: 2;
        text-align: center;
    }

    .content h2{
        font-size: 40px;
    }
    
    .content>span>ul{
        padding: 0px;
    }

    .container ul::after{
        content: "";
        margin: 2px;
    }

    .genres{
        color: #ffffffd0;
        font-weight: 500;
        font-size: large;
        margin: 10px;
    }

    .runtime::before{
        content: '';
        margin: 3px;
    }

    .runtime{
        margin-top: 10px;
    }
}