.container{
    border: 1px solid black;
    background-color: rgb(39, 40, 42);
    display: grid;
    grid-template-columns: 1fr 3fr 3fr 3fr;
    color: rgb(178, 178, 178);
}

.container img {
    grid-row: 1 / span 3;
}

.container h2 {
    border-left: 10px;
    grid-column: 2 / span 1;
    margin: 50px;
}

.container p {
    border-left: 10px;
    grid-column: 2 / span 3;
    border-bottom: 97px;
    margin: 1;
    margin: 50px;
}

.container ul::before{
    content: 'Genrs';
    color: #7f4af6d6;
    font-size: 31px;
    font-weight: 1000;
    margin-left: -23px;
}

.container ul{
    grid-column: 4;
    grid-row: 1;
    color: white;
    marker: none;
}

@media (width<670px) {
    .container h2>a,.container h2, .container p{
        text-align: center;
        margin: 27px 13px;
    }

}